/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
list, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, list, nav, section {
    display: block;
}
*, *::before, *::after{
    box-sizing: border-box;
}
*{
    outline: 1px solid black;
}
body {
    font-family: "Noto Sans TC", Roboto, sans-serif;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
}
header, footer{
  background-color: #AA0601;
  color: #fff;
}
.container{
  width: 1296px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

img{
  vertical-align: middle;
}
.navi{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
}
.navi a{
  display: block;
  text-decoration: none;
  color: #fff;
  padding: 10px 32px;
}
.navi a:hover{
  background-color: #fff;
  border-radius: 50px;
  color: #AA0601;
}
.container-footer{
  width: 1296px;
  margin: 0 auto;
}
.footer-nav{
  display: flex;
  justify-content: space-between;
}
.footer-nav-list{
  display: flex;
  align-items: center;
  list-style: none;
}
.footer-nav-list a{
  display: block;
  text-decoration: none;
  color: #fff;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  padding: 12px 24px;
}
.footer-nav-list a:hover{
  background-color: #fff;
  border-radius: 50px;
  color: #AA0601;
}
.footer-nav-sns{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
}
.footer-nav-sns img{
  height: 48px;
}
.footer-nav-sns a{
  display: block;
  text-decoration: none;
  margin-left: 32px;
}
.contact{
  list-style: none;
}
.contact a{
  text-decoration: none;
  color: #fff;
}
