.footer-legal{
  border-top: 1px solid #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer-legal-list{
  display: flex;
  list-style: none;
}
.footer-legal-list a{
  display: block;
  text-decoration: none;
  color: #fff;
